    // 图表数据初始化
    showChart() {
      // 数据源

      // Step 1: 创建 Chart 对象
      var chart = new F2.Chart({
        id: "mountNode",
        pixelRatio: window.devicePixelRatio
      });

      // Step 2: 载入数据源
      chart.source(this.data, {
        value: {
          tickCount: 5,
          min: 0
        },
        date: {
          type: "timeCat",
          range: [0, 1],
          tickCount: 3
        }
      });
      chart.tooltip({
        custom: true,
        showXTip: true,
        showYTip: true,
        snap: true,
        crosshairsType: "xy",
        crosshairsStyle: {
          lineDash: [2],
          stroke: "rgba(255, 0, 0, 1)",
          lineWidth: 2,
          fill: "#33b5e5"
        },
        // 对象用法，直接定义文本的显示样式

